*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  overflow-wrap: break-word;
  user-select: text;
}

body {
  position: relative;
  background-color: gray;
  font-family: Arial, Helvetica, sans-serif;
  font-style: normal;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 100vh;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

input {
  padding: 0;
  margin: 0;
  border: none;
  outline: none;
}

button {
  padding: 0;
  border: none;
  cursor: pointer;
  outline: none;
}

ol,
ul {
  padding-left: 0;
}

dl,
ol,
ul {
  margin-top: 0;
  margin-bottom: 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0;
}

.containers {
  max-width: 1440px;
  padding: 0 90px;
  margin: 0 auto;
  width: 100%;
}

.display-n {
  display: none !important;
}

opacity-h {
  opacity: 0 !important;
}

.header {
  width: 100%;
  height: 70px;
  background: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 30px;
  width: 100%;
}

.header__block {
  width: max-content;
  display: flex;
  align-items: center;
}

.header__block:first-child {
  justify-content: flex-start;
  gap: 32px;
}

.header__block:last-child {
  justify-content: flex-end;
  gap: 30px;
}

.header__l {
  cursor: pointer;
}

.header__s {
  font-weight: 400;
  font-size: 10px;
  line-height: 13px;
  color: #262626;
}

.header__e {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 11px;
}

.header__e p {
  font-weight: 400;
  font-size: 15px;
  line-height: 19px;
  letter-spacing: -0.5px;
  color: #140848;
}

.header__p {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 11px;
}

.header__p p {
  font-weight: 400;
  font-size: 15px;
  line-height: 19px;
  letter-spacing: -0.5px;
  color: #140848;
}

.header__b {
  background: #f2ce14;
  border-radius: 10px;
  padding: 10px 17px;
  cursor: pointer;
}

.header__b p {
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  color: #141413;
  text-transform: uppercase;
}

.footer {
  background-color: #201547;
  width: 100%;
  min-height: 630px;
  text-align: center;
  color: #ffffff;
}